import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";
import Title from "../base/functional/Title";
import Text from "../base/functional/Text";
import Subtitle from "../base/functional/Subtitle";
import Space from "../base/functional/Space";

export default class Policy extends Component {

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className="app">
                <Header handle={handleStateUpdate.bind(this)} />

                <Space />
                <div className={"fullpage_text"}>
                    <Title title={strings.policy__title} />
                    <Subtitle subtitle={strings.policy__subtitle} />
                    <Text text={strings.policy__text} />
                </div>

                <Space />
                <Footer />
            </div>
        );
    }
}