import React, { Component } from 'react';
import clm from "clmtrackr";
import {
    acos, pow, pi
} from 'mathjs'
import idGenerator from 'react-id-generator';
import * as GestureTypes from "../../../utils/GestureTypes";

const GESTURE_HANDLING_TIMEOUT = 500;
const POSITIONS_LENGTH = 5;
const BROWS_MOVEMENT_LIMIT = 0.008;
const EYES_MOVEMENT_LIMIT = 0.008;
const BLINKS_LIMIT = 0.008;
const STRONG_BLINKS_LIMIT = 0.008;

export class Track extends Component {

    constructor(props) {
        super(props);

        this.buttonId = idGenerator();

        this.isStarted = false;
        this.positions = [];
        this.normalisation = 1;
        this.initial_positions = [];
        this.lastHandledTime = 0;

        this.videoWidth = -1;
        this.videoHeight = -1;

        this.targetGestureId = GestureTypes.GESTURE_NOTHING;
    }

    difference(firstPoint, secondPoint) {
        return pow(pow(firstPoint[0] - secondPoint[0], 2) + pow(firstPoint[1] - secondPoint[1], 2), 0.5)
    }

    average(index) {
        const positions = this.positions;

        if (positions.length === 0) {
            return 0;
        }

        else {
            let sum = 0;

            for (let i = 1; i < positions.length; i++) {
                if (positions[i][index] && positions[i - 1][index]) {
                    sum += this.difference(positions[i][index], positions[i - 1][index]) / positions.length;
                }
            }

            return sum;
        }
    }

    complexAverage(valuesIndex) {
        let sum = 0;

        for (let i = 0; i < valuesIndex.length; i++) {
            sum += this.average(valuesIndex[i]);
        }

        return sum / valuesIndex.length;
    }

    addNewPositions(positions_array) {
        this.positions[this.positions.length] = positions_array;

        if (this.positions.length > POSITIONS_LENGTH) {
            this.positions.shift();
        }
    }

    verifyNosePosition() {
        if (this.positions.length > 0) {
            const positions = this.positions[this.positions.length - 1];
            const initial = this.initial_positions;

            const firstPoint = positions[33];
            const secondPoint = positions[41];
            const thirdPoint = positions[62];

            const firstInitialPoint = initial[33];
            const secondInitialPoint = initial[41];
            const thirdInitialPoint = initial[62];

            if (firstPoint && secondPoint && thirdPoint) {
                const currentNoseLength = this.difference(firstPoint, secondPoint) + this.difference(secondPoint, thirdPoint);
                const currentNoseWidth = thirdPoint[0] - firstPoint[0];
                const cos = currentNoseWidth / currentNoseLength;

                const angle = acos(cos) * 180 / pi;
                const isGoodAngle = angle > 80 && angle < 100;

                const noseMovement = (
                    this.difference(firstPoint, firstInitialPoint)
                    + this.difference(secondPoint, secondInitialPoint)
                    + this.difference(thirdPoint, thirdInitialPoint)
                ) / 3;

                const initialNoseLength = this.difference(firstInitialPoint, secondInitialPoint) + this.difference(secondInitialPoint, thirdInitialPoint);
                const noseMovementDerivative = noseMovement / initialNoseLength;
                const isGoodLength = noseMovementDerivative < 0.15;

                return { is_good_angle: isGoodAngle, is_good_length: isGoodLength }
            }

            else {
                return false;
            }
        }

        else {
            return false;
        }
    }

    checkEyes(type) {
        /*
        1 -- left
        2 -- right
        3 -- down
        4 -- up
         */

        const direction = (type === 2 || type === 4)? 1 : -1;
        const coordinateIndex = (type === 3 || type === 4)? 1 : 0;

        if (this.positions.length > 2) {
            const positions = this.positions[this.positions.length - 1];
            const previousPositions = this.positions[this.positions.length - 2];

            const currentFirstPoint = positions[27];
            const currentSecondPoint = positions[32];

            const previousFirstPoint = previousPositions[27];
            const previousSecondPoint = previousPositions[32];

            if (currentSecondPoint && currentFirstPoint && previousFirstPoint && previousSecondPoint) {
                const firstDifference = currentFirstPoint[coordinateIndex] - previousFirstPoint[coordinateIndex];
                const secondDifference = currentSecondPoint[coordinateIndex] - previousSecondPoint[coordinateIndex];

                const limit = EYES_MOVEMENT_LIMIT * direction;
                const result = (firstDifference + secondDifference) / (2 * this.normalisation);

                console.log("Checking eyes of type " + type + ": " + result);
                return (direction === -1)? result < limit : result > limit;
            }

            else {
                return false;
            }
        }

        else {
            return false;
        }
    }

    checkBlinks(type) {
        // left -- 1, right -- 2
        const valuesIndex = type === 1? [30, 68, 29, 67, 28, 70, 31, 69] : [23, 63, 24, 64, 25, 65, 26, 66];

        if (this.positions.length > 0) {
            const result = this.complexAverage(valuesIndex) / this.normalisation;

            console.log("Checking blinks of type " + type + ": " + result);
            return result > BLINKS_LIMIT;
        }

        else {
            return false;
        }
    }

    checkStrongBlinks(type) {
        const valuesIndex = type === 1? [30, 68, 29, 67, 28, 70, 31, 69] : [23, 63, 24, 64, 25, 65, 26, 66];

        if (this.positions.length > 0) {
            const result = this.complexAverage(valuesIndex) / this.normalisation;
            console.log("Checking strong blinks of type " + type + ": " + result);

            return result > STRONG_BLINKS_LIMIT;
        }

        else {
            return false;
        }
    }

    checkBrows(type) {
        // left -- 1, right -- 2
        const valuesIndex = type === 1? [15, 16, 17, 18] : [19, 20, 21, 22];

        if (this.positions.length > 0) {
            const result = this.complexAverage(valuesIndex) / this.normalisation;
            console.log("Checking brows of type " + type + ": " + result);

            return result > BROWS_MOVEMENT_LIMIT;
        }

        else {
            return false;
        }
    }

    getPositioningError(checkingPositionResult) {
        let action = "";

        if (!checkingPositionResult.is_good_angle) {
            action += "   "
        }

        if (!checkingPositionResult.is_good_length) {
            if (action.length > 0) {
                action += ",";
            }

            action += "\n  "
        }

        return action;
    }

    canHandleGesture() {
        return (new Date()).getTime() - GESTURE_HANDLING_TIMEOUT > this.lastHandledTime;
    }

    componentDidUpdate() {
        if (this.videoWidth === -1 && this.videoHeight === -1) {
            return;
        }

        console.log("videoWidth: " + this.videoWidth + " this.videoHeight: " + this.videoHeight);

        let link = this;
        let video = document.getElementById('webcam');
        let canvas = document.getElementById('canvas');

        const context = canvas.getContext('2d');

        let tracker = new clm.tracker({
            faceDetection: {
                useWebWorkers: false,
            },
        });

        tracker.init();
        tracker.start(video);

        function positionLoop() {
            requestAnimationFrame(positionLoop);

            if (tracker.getCurrentPosition()) {
                context.clearRect(0, 0, canvas.width, canvas.height);
                tracker.draw(canvas);
            }

            const positions = tracker.getCurrentPosition();

            if (!link.isStarted) {
                return;
            }

            if (link.initial_positions.length === 0) {
                link.initial_positions = positions;
                link.normalisation = link.difference(positions[33], positions[62]);
            }

            link.addNewPositions(positions);

            let gestureDetectionId = GestureTypes.GESTURE_NOTHING;
            let checkingPositionResult = link.verifyNosePosition();

            if (!link.canHandleGesture()) {
                return;
            }

            if (checkingPositionResult.is_good_angle
                && checkingPositionResult.is_good_length) {

                if (
                    link.targetGestureId === GestureTypes.GESTURE_EYES_BLINK_BOTH ||
                    link.targetGestureId === GestureTypes.GESTURE_EYES_BLINK_LEFT ||
                    link.targetGestureId === GestureTypes.GESTURE_EYES_BLINK_RIGHT
                ) {
                    const blinkLeft = link.checkBlinks(1);
                    const blinkRight = link.checkBlinks(2);

                    if (blinkLeft && blinkRight) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_BLINK_BOTH;
                    }

                    else if (blinkLeft) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_BLINK_LEFT;
                    }

                    else if (blinkRight) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_BLINK_RIGHT;
                    }
                }

                else if (
                    link.targetGestureId === GestureTypes.GESTURE_BOTH_BROWS_UP ||
                    link.targetGestureId === GestureTypes.GESTURE_RIGHT_BROW_UP ||
                    link.targetGestureId === GestureTypes.GESTURE_LEFT_BROW_UP
                ) {
                    const leftBrowIsUp = link.checkBrows(1);
                    const rightBrowIsUp = link.checkBrows(2);

                    if (leftBrowIsUp && rightBrowIsUp) {
                        gestureDetectionId = GestureTypes.GESTURE_BOTH_BROWS_UP;
                    }

                    else if (leftBrowIsUp) {
                        gestureDetectionId = GestureTypes.GESTURE_LEFT_BROW_UP;
                    }

                    else if (rightBrowIsUp) {
                        gestureDetectionId = GestureTypes.GESTURE_RIGHT_BROW_UP;
                    }
                }

                else {
                    const eyesLeft = link.checkEyes(1);
                    const eyesRight = link.checkEyes(2);
                    const eyesDown = link.checkEyes(3);
                    const eyesUp = link.checkEyes(4);

                    if (eyesLeft) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_LEFT;
                    }

                    else if (eyesRight) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_RIGHT;
                    }

                    else if (eyesDown) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_DOWN;
                    }

                    else if (eyesUp) {
                        gestureDetectionId = GestureTypes.GESTURE_EYES_UP;
                    }
                }

                link.lastHandledTime = (new Date()).getTime();
                console.log("GestureId: " + gestureDetectionId);

                try {
                    window.Android.onSucceed();
                    window.Android.onPerformed(gestureDetectionId);
                }

                catch (e) {
                    console.log(e)
                }
            }

            else {
                let error = link.getPositioningError(checkingPositionResult);

                try {
                    window.Android.onErrored(error);
                }

                catch (e) {
                    console.log(e)
                }
            }
        }

        positionLoop();
    }

    start() {
        console.log("Called start");

        this.isStarted = true;
        this.initial_positions = [];
        this.positions = [];
    }

    stop() {
        console.log("Called stop");

        this.isStarted = false;
        this.initial_positions = [];
        this.positions = [];
    }

    getGestureId() {
        this.targetGestureId = window.location.href.split("target=")[1];
    }

    componentDidMount() {
        window.startEvent = new Event('start');
        window.stopEvent = new Event('stop');

        let link = this;

        window.addEventListener('start', function() {
            link.start();
        }, false);

        window.addEventListener('stop', function() {
            link.stop();
        }, false);

        if (window.location.href.indexOf("target") !== -1) {
            this.getGestureId();
        }

        let video = document.getElementById('webcam');

        function onStreaming(stream) {
            video.srcObject = stream;
        }

        navigator.mediaDevices.getUserMedia({ audio: false, video: true }).then(onStreaming);

        if (this.videoWidth === -1 && this.videoHeight === -1) {
            const getVideoSize = function () {
                link.videoWidth = video.videoWidth;
                link.videoHeight = video.videoHeight;
                video.removeEventListener('playing', getVideoSize, false);
                link.forceUpdate()
            };

            video.addEventListener('playing', getVideoSize, false);
        }
    }


    render() {
        let containerWidth = (this.videoWidth === -1)? 500 : this.videoWidth;
        let containerHeight = (this.videoHeight === -1)? 400 : this.videoHeight;

        return (
            <div className="track">
                <div className="overlaying">
                    <video id="webcam" width={containerWidth} height={containerHeight} autoPlay={true}/>
                    <canvas width={containerWidth} height={containerHeight} id="canvas"/>
                </div>
            </div>
        )
    }
}