import React, { Component} from 'react';

export default class Star extends Component {

    render() {
        let src = this.props.isActive?
            "images/main/footer/star_active.svg" : "images/main/footer/star_disabled.svg";

        return (
            <div className={"rate_form__star"} id={this.props.id}>
                <img className={"rate_form__star__img"} src={src}/>
            </div>
        )
    }
}