import React, {Component} from 'react';

export default class VideoButton extends Component {
    render() {
        let path = this.props.isNext? "images/main/functional/next.svg" : "images/main/functional/previous.svg";

        return (
            <div className={"functional__phone_container__video_button"} id={this.props.id}>
                <img className={"functional__phone_container__video_button__image"} src={path} />
            </div>
        )
    }
}