import React, {Component} from "react";
import nextId from "react-id-generator";
import axios from 'axios';
import cookie from 'react-cookies'
import strings from "../../Localisation";
import version from "../../../config"
import {sendAmplitudeData} from "../../../utils/Amplitude";
import {ServerLink} from "../../../utils/ServerLink";
let classNames = require('classnames');

export default class ShortForm extends Component {
    constructor(props) {
        super(props);

        this.buttonId = nextId();
        this.inputId = nextId();

        let email = cookie.load("email");

        this.state = {
            user_email__text: email? email : "",
            is_error: undefined,
            toast: undefined
        }
    }

    sendRequest() {
        sendAmplitudeData("Preorder: sending form");

        const mail = document.getElementById(this.inputId).value.replace("\ \g", "");
        const link = this;

        if (mail.length > 0) {
            if (ServerLink.getMailRegex().test(mail)) {
                ServerLink.sendEmailRequest(mail)
                    .then(function (response) {
                        link.setSuccess("  ", mail)

                    })
                    .catch(function (error) {
                        console.log(error.message);

                        if (error.message.includes("Network Error")) {
                            link.setSuccess("  ", mail);
                        }

                        else {
                            link.setError("  ", mail);
                        }
                    });
            }

            else {
                this.setError("  ")
            }
        }

        else {
            this.setError("  ")
        }
    }

    setError(message, mail) {
        cookie.save("email",  mail);
        this.setState(
            {
                is_error: true,
                toast: message,
                user_email__text: mail
            }
        )
    }

    setSuccess(message, mail) {
        cookie.save("email",  mail);
        this.setState(
            {
                is_error: false,
                toast: message,
                user_email__text: mail
            }
        )
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let input = document.getElementById(this.inputId);
        const link = this;

        input.addEventListener('submit', function(e) {
            console.log(e);
            e.preventDefault();
            button.click();
        });

        input.addEventListener('keypress', function (e) {
            let key = e.key || e.altKey;
            console.log(key);

            if (key === 'Enter') {
                e.preventDefault();
                button.click();
            }
        });

        button.addEventListener("click", function(event) {
            event.preventDefault();
            link.sendRequest();
        });
    }

    render() {
        let classNameToast = classNames("preorder__short_form__toast", {
            "preorder__short_form__toast__error": this.state.is_error === true,
            "preorder__short_form__toast__success": this.state.is_error === false
        });

        return (
            <div>
                <div className={"preorder__short_form"}>
                    <div className={"preorder__short_form__content"}>
                        <input type="text" size="20" placeholder={"your@email"} id={this.inputId} className={"preorder__input"}/>
                        <div id={this.buttonId} className={"preorder__button"}>
                            <img src={"images/main/preorder/send.svg"} />
                        </div>
                    </div>
                </div>
                <div className={classNameToast}>{this.state.toast}</div>
            </div>
        )
    }
}