import React, { Component} from 'react';
import nextId from "react-id-generator";

export default class VideoPhone extends Component {
    constructor(prop) {
        super(prop);

        this.videoId = nextId();
        this.phoneId = nextId();
    }

    render() {
        return (
            <div id={this.props.id} className={"functional__phone"}>
                <div className={"functional__phone_container__phone"}>
                    <img className={"functional__phone_container__phone_corpus"} id={this.phoneId} src={"/images/main/functional/phone.png"}/>
                </div>

                <video
                    src={this.props.src}
                    playsInline
                    autoPlay={true}
                    controls={false}
                    muted
                    className={"functional__phone_container__video"}
                    id={this.videoId} />
            </div>
        )
    }
}