import React, {Component} from "react";
import nextId from "react-id-generator";
import cookie from 'react-cookies'
import {sendAmplitudeData} from "../../../../utils/Amplitude";
import {ServerLink} from "../../../../utils/ServerLink";
import strings from "../../../Localisation";
let classNames = require('classnames');

export default class ShortForm extends Component {
    constructor(props) {
        super(props);

        this.buttonId = nextId();
        this.inputId = nextId();

        let comment = cookie.load("comment");

        this.state = {
            comment__text: comment? comment : "",
            is_error: undefined,
            toast: undefined
        }
    }

    sendRequest() {
        sendAmplitudeData("Footer: sending comment");

        const comment = document.getElementById(this.inputId).value.replace("\ \g", "");
        const link = this;

        if (comment.length > 0) {
            ServerLink.sendComment(comment)
                .then(function (response) {
                    link.setSuccess("  ", comment)

                })
                .catch(function (error) {
                    console.log(error.message);

                    if (error.message.includes("Network Error")) {
                        link.setSuccess("  ", comment);
                    }

                    else {
                        link.setError("  ", comment);
                    }
                });
        }

        else {
            this.setError("  ")
        }
    }

    setError(message, mail) {
        cookie.save("comment",  mail);
        this.setState(
            {
                is_error: true,
                toast: message,
                comment__text: mail
            }
        )
    }

    setSuccess(message, mail) {
        cookie.save("comment",  mail);
        this.setState(
            {
                is_error: false,
                toast: message,
                comment__text: mail
            }
        )
    }

    recountLines(element) {
        let linesCount = element.value.split("\n").length;
        linesCount = linesCount === 0? 1 : linesCount;
        element.rows = linesCount;
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let input = document.getElementById(this.inputId);
        const link = this;

        input.addEventListener('submit', function(e) {
            console.log(e);
            e.preventDefault();
            button.click();
        });

        input.addEventListener('keypress', function (event) {
            let key = event.key || event.altKey;

            if (key === 'Enter') {
                event.preventDefault();
                const content = this.value;

                if(event.shiftKey){
                    this.value = content + "\n";
                }

                else {
                    button.click();
                }

                link.recountLines(this);
            }

            else if( key === 'Backspace' || key === 'Delete' ) {
                event.preventDefault();
                link.recountLines(this);
            }
        });

        button.addEventListener("click", function(event) {
            event.preventDefault();
            link.sendRequest();
        });
    }

    render() {
        let classNameToast = classNames("preorder__short_form__toast", {
            "preorder__short_form__toast__error": this.state.is_error === true,
            "preorder__short_form__toast__success": this.state.is_error === false
        });

        return (
            <div>
                <div className={"footer__short_form"}>
                    <div className={"preorder__short_form__content"}>
                        <textarea type="text" rows={1} placeholder={strings.footer__rate_form__input} id={this.inputId} className={"footer__input"}/>
                        <div id={this.buttonId} className={"preorder__button"}>
                            <img src={"images/main/preorder/send.svg"} />
                        </div>
                    </div>
                </div>
                <div className={classNameToast}>{this.state.toast}</div>
            </div>
        )
    }
}