import axios from "axios";
import version from "../config";
import strings from "../components/Localisation";

export class ServerLink {

    static getMailRegex() {
        return new RegExp("(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|\"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])*\")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\\])")
    }

    static sendEmailRequest(mail) {
        const language = strings.getLanguage();
        return axios.get("https://maker.ifttt.com/trigger/email_sent/with/key/dZ2N1BVOi6c5zCIOD27V9Q?value1=" + mail  + "&value2=" + language + "&value3=" + version.version)
    }

    static sendStar(stars) {
        const language = strings.getLanguage();
        return axios.get("https://maker.ifttt.com/trigger/star_set/with/key/dZ2N1BVOi6c5zCIOD27V9Q?value1=" + stars  + "&value2=" + language + "&value3=" + version.version)
    }

    static sendComment(comment) {
        const language = strings.getLanguage();
        return axios.get("https://maker.ifttt.com/trigger/comment_set/with/key/dZ2N1BVOi6c5zCIOD27V9Q?value1=" + comment  + "&value2=" + language + "&value3=" + version.version)
    }
}