import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../../utils/Amplitude";
let classNames = require('classnames');

export default class Logo extends Component {
    constructor(props) {
        super(props);
        this.buttonId = nextId();
    }

    componentDidMount() {
        let element = document.getElementById(this.buttonId);
        element.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Header: clicked on logo");
            window.open("/", '_self');
        })
    }

    /*
    render() {
        return (
            <div className={"header_logo"} id={this.buttonId}>Reface</div>
        )
    }

     */

    render() {
        let className = classNames('header_logo__mobile', {
            'header_logo__mobile__active': this.props.isActive === true,
        });

        return (
            <div className={className} id={this.buttonId}>
                <img className={"header_logo__image"} src={"images/main/header/logo_mobile.svg"}/>
            </div>
        )
    }
}