import React, { Component} from 'react';
import nextId from "react-id-generator";
import strings from "../../../Localisation";
import Star from "./Star";
import {ServerLink} from "../../../../utils/ServerLink";
import ShortForm from "./ShortForm";
import {sendAmplitudeData} from "../../../../utils/Amplitude";

export default class RateForm extends Component {

    constructor(props) {
        super(props);

        this.activeIndex = -1;
        this.stars = [];
        for (let i = 0; i < 5; i++) {
            this.stars[this.stars.length] = nextId();
        }
    }

    getStars() {
        let link = this;

        return this.stars.map(function(element, index) {
            console.log(index);

            return <Star
                id={element}
                isActive={index <= link.activeIndex} />
        });
    }

    componentDidMount() {
        let link = this;

        for (let id of this.stars) {
            let element = document.getElementById(id);

            element.addEventListener('click', function(event) {
                link.activeIndex = link.stars.indexOf(this.id);
                let stars = link.activeIndex + 1;

                sendAmplitudeData("Footer: sending star");

                ServerLink.sendStar(stars)
                    .then(function (response) {
                        console.log("Everything was sent");

                    })
                    .catch(function (error) {
                        console.log(error.message);
                    });

                event.preventDefault();
                link.forceUpdate()
            });
        }
    }

    render() {
        return (
            <div className={"rate_form"}>
                <div className={"rate_form__title"}>
                    {strings.footer__rate_form__title}
                </div>
                <div className={"rate_form__description"}>
                    {strings.footer__rate_form__description}
                </div>
                <div className={"rate_form__stars"}>
                    {
                        this.getStars()
                    }
                </div>
                <ShortForm />
            </div>
        )
    }
}